/* General styles */
body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.home-container {
  width: 100%;
  height: 100vh;
  background-color: #f0f0f0;
}

/* Navigation Menu */
.menu {
  background-color: #333;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Logo and Title */
.logo-title {
  display: flex;
  align-items: center;
}

.logo {
  width: 40px;
  height: auto;
  margin-right: 15px;
}

.menu h1 {
  font-size: 1.5rem;
  font-weight: bold;
  color: white;
}

/* Hamburger icon (always visible) */
.hamburger {
  font-size: 1.5rem;
  color: white;
  cursor: pointer;
}

/* Menu items (hidden by default) */
.menu-items {
  display: none;
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu-items.open {
  display: block;
}

.menu-items li {
  margin: 10px 0;
}

.menu-items a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
}